#maintable {
  margin: 0 auto;
  padding-top: 30px;
  padding: 25px;
  font-family: "Quattrocento", serif;
}

#maintable td {
  padding: 10px;
}

#formtable {
  border: 3px solid #204051;
  border-radius: 20px;
}
#formtable tr td {
  text-align: center;
  text-transform: uppercase;
  font-family: "Oswald", sans-serif;
}
#formtable input,
textarea {
  border-radius: 20px;
  outline: none;
  font-family: "Quattrocento", serif;
  border: 3px solid #204051;
}
#formtable input:hover,
textarea:hover {
  cursor: pointer;
}

#formtable input:focus,
textarea:focus {
  width: 150px;
  border: 3px solid #204051;
}

#formtable #submitbutton {
  background-color: #204051;
  font-size: 1.5rem;
  display: block;
  color: white;
  border: none;
  outline: none;
  padding: 2rem;
  width: 100%;
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
}

#formtable #submitbutton:hover {
  box-shadow: 1px 1px 10px #999;
  text-decoration: underline;
  cursor: pointer;
}
body {
  background: #1c92d2; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #f2fcfe,
    #1c92d2
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #f2fcfe,
    #1c92d2
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

#equipmentstable tr td {
  border: 3px solid #204051;
  text-align: center;
  padding: 2px;
  border-radius: 20px;
}

#equipmentstable tr td img {
  margin: 0 auto;
  border-radius: 50%;
}

#equipmentstable tr td img:hover {
  text-decoration: underline;
  position: inherit;
  margin-left: 0;
  margin-right: 0;
  width: 100%;
  height: auto;
  object-fit: cover;
  transform: scale(1.25);
}
